/* eslint-disable no-undef */
/* eslint-disable no-restricted-syntax */
const h = document.querySelectorAll('.header--main-menu ul li');

function addClass() {
  for (const e of h) {
    if (e.firstChild.href === document.location.href) {
      const $a = e.firstChild;
      $a.classList.add('active');
    }
  }
}

window.onload = addClass();
